<template>
	<view class="content">
		<view class="title">简单数字游戏</view>
		<view class="gameView">
			<view @tap="go2(game.url)" v-for="(game,i) in gamesList" :key="'game-'+i">
				<image :src="game.image || '../../static/logo.png'" mode="widthFix"></image>{{game.name}}
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				gamesList:[
					{
						image:'../../static/sudoku.png',
						url:'../sudoku/sudoku',
						name:'数独'
					},
					{
						image: '../../static/nummark.png',
						url:'../NumberMask/NumberMask',
						name:'数字寻宝'
					},
					{
						image: '../../static/nonogram.png',
						url:'../Nonogram/Nonogram',
						name:'以数织图'
					},
					{
						image:'../../static/numcut.png',
						url:'../NumberCut/NumberCut',
						name:'拆数专家'
					},
				]
			}
		},
		onLoad() {

		},
		methods: {
			go2(page){
				uni.navigateTo({
					url:page
				})
			},
		}
	}
</script>

<style>
	.gameView{
		display: block;
		width: 90%;
		padding: 5%;
	}
	
	.gameView view{
		box-shadow: 0 2px 5px #333333;
		width: 24%;
		padding: 2% 1.5%;
		border-radius: 10px;
		background-color: #FFFFFF;
		float: left;
		display: flex;
		flex-direction: column;
		align-items: center;
		overflow: hidden;
		margin: 2% 10px;
	}
	
	.gameView view:active{
		background-color: #999999;
	}
	
	.gameView view image{
		width: 95%;
	}
</style>
